import React, { useEffect, useState } from "react";
import firedb from "../firebase";
import "./Blog2.css";

function Blog(props) {
  const initialize = {
    title: "",
    description: "",
    creator: "",
  };
  const [values, setValues] = useState(initialize);

  useEffect(() => {
    if (props.currentId == "")
      setValues({
        ...initialize,
      });
    else {
      setValues({
        ...props.content[props.currentId],
      });
    }
  }, [props.currentId, props.content]);

  const handleInputChage = (e) => {
    const { name, value } = e.target;
    setValues({
      ...values,
      [name]: value,
    });
  };

  const handleFormSubmit = (event) => {
    event.preventDefault();

    props.addOrEdit(values);
  };
  const l = (event) => {
    event.preventDefault();
    console.log(values);
  };
  return (
    <>
      <div className="row create">
        <div className="container p-3">
          <h1 className="text-center font-weight-bold text-uppercase text-primary">
            Blog website
          </h1>
          <h1 className="text-center font-weight-bold text-uppercase text-secondary">
            create Blog website
          </h1>
          <form className="centered" onSubmit={handleFormSubmit}>
            <div class="form-group row my-5">
              <label for="inputTitle" class="col-sm-1 col-form-label">
                Title :
              </label>
              <div class="col-sm-5">
                <input
                  name="title"
                  type="text"
                  class="form-control"
                  id="inputTitle"
                  placeholder="Title"
                  value={values.title}
                  onChange={handleInputChage}
                />
              </div>
            </div>
            <div class="form-group row my-5">
              <label for="inputDescription" class="col-sm-1 col-form-label">
                Description:
              </label>
              <div class="col-sm-5">
                <textarea
                  name="description"
                  class="form-control"
                  id="inputDescription"
                  rows="3"
                  placeholder="Description"
                  value={values.description}
                  onChange={handleInputChage}
                />
              </div>
            </div>
            <div class="form-group row my-5">
              <label for="inputCreator" class="col-sm-1 col-form-label">
                Creator :
              </label>
              <div class="col-sm-5">
                <input
                  name="creator"
                  type="text"
                  class="form-control"
                  id="inputCreator"
                  placeholder="Creator name"
                  value={values.creator}
                  onChange={handleInputChage}
                />
              </div>
            </div>
            <button type="submit" class="btn btn-primary">
              {props.currentId == "" ? "create a blog" : "Update"}
            </button>
          </form>
        </div>
      </div>
    </>
  );
}

export default Blog;
