import React from "react";
import { useEffect, useState } from "react";
import firedb from "../firebase";

import "./Blog2.css";

function Bloging(props) {
  // const [content, setContent] = useState({});
  // useEffect(() => {
  //   firedb.child("blog").on("value", (snapshot) => {
  //     if (snapshot.val() != null) {
  //       setContent({
  //         ...snapshot.val(),
  //       });
  //     }
  //   });
  // }, []);

  const onDelete = (key) => {
    if (window.confirm("Are sure to delete this record"))
      firedb.child(`blog/${key}`).remove((err) => {
        if (err) console.log(err);
        else props.setCurrentId("");
      });
  };
  return (
    <>
      <div className="Blog">
        <h1>Blogs</h1>
        {/* {console.log(props.content)} */}
        {Object.keys(props.content).map((id) => {
          console.log(props.content[id.creator]);
          return (
            <div className="Blog-box1">
              <div className="Blog-box">
                <h1>Title: {props.content[id].title}</h1>
                <p>Description: {props.content[id].description}</p>
                <h4>Creator: {props.content[id].creator}</h4>
                <a
                  className="btn text-primary"
                  onClick={() => {
                    props.setCurrentId(id);
                  }}
                >
                  <i className="fas fa-pencil-alt"></i>
                </a>
                <a className="btn text-danger" onClick={() => onDelete(id)}>
                  <i className="fas fa-trash-alt"></i>
                </a>
              </div>
            </div>
          );
        })}
      </div>
    </>
  );
}

export default Bloging;
