import { useState, useEffect } from "react";
import "./App.css";
import Blog from "./component/Blog";
import Blogging from "./component/Bloging";
import firedatabase from "./firebase";

function App() {
  const [content, setContent] = useState({});
  useEffect(() => {
    firedatabase.child("blog").on("value", (snapshot) => {
      if (snapshot.val() != null) {
        setContent({
          ...snapshot.val(),
        });
      }
    });
  }, []);
  const [currentId, setCurrentId] = useState("");
  const addOrEdit = (values) => {
    if (currentId == "")
      firedatabase.child("blog").push(values, (err) => {
        if (err) console.log(err);
        else setCurrentId("");
      });
    else
      firedatabase.child(`blog/${currentId}`).set(values, (err) => {
        if (err) console.log(err);
        else setCurrentId("");
      });
  };
  // setCurrentId={setCurrentId}
  return (
    <div>
      <Blog {...{ addOrEdit, currentId, content }} />
      <Blogging {...{ setCurrentId, content }} />
    </div>
  );
}

export default App;
